import icons from "url:../../img/icons.svg";
import View from './view.js';

import { Fraction } from 'fraction.js';
import eventStore from '../pubSub.js';

class RecipeView extends View {


  constructor() {
    super(document.querySelector(".recipe"),
    'An Error occurred while fetching the recipe.',
    'Start by searching for a recipe or an ingredient. Have fun!');
      ['load', 'hashchange'].forEach(ev => window.addEventListener(ev, eventStore.publish.bind(eventStore, `recipeView.${ev}`)));
    
      // Event Delegation
      this._parentEl.addEventListener('click', (e) => {
        this._updateServingsEvent(e);
        this._toggleBookmark(e);
      });
  }

  _updateServingsEvent(e) {
    const btn = e.target.closest('.btn--tiny');

    if (!btn) return;

    let { servingsTo } = btn.dataset;

    servingsTo = +servingsTo;

    if (servingsTo < 1) return;

    eventStore.publish('servings.update', servingsTo);
  }
  
  _toggleBookmark(e) {
    const btn = e.target.closest('.btn--bookmark');

    if (!btn) return;

    ['bookmark.click'].forEach(event => eventStore.publish(event, this._data));


  }


  _constructHTML() {
    return `
          <figure class="recipe__fig">
            <img src="${this._data.imageUrl}" alt=">${
      this._data.title
    }" class="recipe__img">
            <h1 class="recipe__title">
                <span>${this._data.title}</span>
            </h1>
          </figure>
      
          <div class="recipe__details">
            <div class="recipe__info">
                <svg class="recipe__info-icon">
                  <use href="${icons}#icon-clock"></use>
                </svg>
                <span class="recipe__info-data recipe__info-data--minutes">45</span>
                <span class="recipe__info-text">minutes</span>
              </div>
              <div class="recipe__info">
                <svg class="recipe__info-icon">
                  <use href="${icons}#icon-users"></use>
                </svg>
                <span class="recipe__info-data recipe__info-data--people">${
                  this._data.servings
                }</span>
                <span class="recipe__info-text">servings</span>
                <div class="recipe__info-buttons">
                  <button class="btn--tiny btn--decrease-servings" data-servings-to="${this._data.servings - 1}">
                    <svg>
                      <use href="${icons}#icon-minus-circle"></use>
                    </svg>
                  </button>
                  <button class="btn--tiny btn--increase-servings" data-servings-to="${this._data.servings + 1}">
                    <svg>
                      <use href="${icons}#icon-plus-circle"></use>
                    </svg>
                  </button>
                </div>
          </div>
          <div class="recipe__user-generated ${this._data.key ? '' : 'hidden'}">
            <svg>
              <use href="${icons}#icon-user"></use>
            </svg>
          </div>
          <button class="btn--round btn--bookmark">
            <svg class="">
              <use href="${icons}#icon-bookmark${this._data.bookmarked ? '-fill' : ''}"></use>
            </svg>
          </button>
          </div>
          <div class="recipe__ingredients">
            <h2 class="heading--2">Recipe ingredients</h2>
            <ul class="recipe__ingredient-list">
              ${this._data.ingredients.map(ingredient => this._constructIngredientHTML(ingredient)).join("")}
            </ul>
          </div>
          <div class="recipe__directions">
            <h2 class="heading--2">How to cook it</h2>
            <p class="recipe__directions-text">
              This recipe was carefully designed and tested by
              <span class="recipe__publisher">${
                this._data.publisher
              }</span>. Please check out
              directions at their website.
            </p>
            <a
              class="btn--small recipe__btn"
              href="${this._data.sourceUrl}"
              target="_blank"
            >
              <span>Directions</span>
              <svg class="search__icon">
                <use href="${icons}#icon-arrow-right"></use>
              </svg>
            </a>
          </div>
        `;
  }

  _constructIngredientHTML(ingredient) {
    return `
        <li class="recipe__ingredient">
          <svg class="recipe__icon">
            <use href="${icons}#icon-check"></use>
          </svg>
          <div class="recipe__quantity">${ingredient.quantity ? Fraction(ingredient.quantity).toFraction(true) : ''}</div>
          <div class="recipe__description">
            <span class="recipe__unit">${ingredient.unit}</span>
            ${ingredient.description}
          </div>
        </li>
        `;
  }
}

export default new RecipeView();
