import View from '../views/view.js';
import eventStore from '../pubSub.js';
import icons from "url:../../img/icons.svg";

class UploadRecipeView extends View {
    _form;
    _closeBtn;
    _openBtn = document.querySelector('.nav__btn--add-recipe');
    _overlay = document.querySelector('.overlay');

    constructor() {
        super(document.querySelector('.add-recipe-window'), '', '');
        this.render({});
        this._form = document.querySelector('.upload');
        this._closeBtn = document.querySelector('.btn--close-modal');
        this._submitForm();
        this._openModal();
        this.closeModal();
    }

    _submitForm() {
        this._parentEl.addEventListener('submit', (e) => {
            e.preventDefault();
            eventStore.publish('uploadRecipeView.submit', [...new FormData(this._form)]);
        })
    }

    toggleUploadModal() {
        debugger;
        if (this._parentEl.classList.contains('hidden')) {
            this.render({});
            this._closeBtn = document.querySelector('.btn--close-modal');
            this._closeBtn.addEventListener('click', (e) => this.toggleUploadModal());
        }
        this._parentEl.classList.toggle('hidden');
        this._overlay.classList.toggle('hidden');

    }

    _openModal() {
        this._openBtn.addEventListener('click', (e) => this.toggleUploadModal());
    }

    closeModal() {
        console.log(this._closeBtn);
        this._overlay.addEventListener('click', (e) => this.toggleUploadModal());
        this._closeBtn.addEventListener('click', (e) => this.toggleUploadModal());
        console.log(this._closeBtn);
    }
    

    _constructHTML() {
        return `
        <button class="btn--close-modal">&times;</button>
        <form class="upload">
          <div class="upload__column">
            <h3 class="upload__heading">Recipe data</h3>
            <label>Title</label>
            <input value="TEST123" required name="title" type="text" />
            <label>URL</label>
            <input value="TEST123" required name="sourceUrl" type="text" />
            <label>Image URL</label>
            <input value="TEST123" required name="image" type="text" />
            <label>Publisher</label>
            <input value="TEST123" required name="publisher" type="text" />
            <label>Prep time</label>
            <input value="23" required name="cookingTime" type="number" />
            <label>Servings</label>
            <input value="23" required name="servings" type="number" />
          </div>
  
          <div class="upload__column">
            <h3 class="upload__heading">Ingredients</h3>
            <label>Ingredient 1</label>
            <input
              value="0.5,kg,Rice"
              type="text"
              required
              name="ingredient-1"
              placeholder="Format: 'Quantity,Unit,Description'"
            />
            <label>Ingredient 2</label>
            <input
              value="1,,Avocado"
              type="text"
              name="ingredient-2"
              placeholder="Format: 'Quantity,Unit,Description'"
            />
            <label>Ingredient 3</label>
            <input
              value=",,salt"
              type="text"
              name="ingredient-3"
              placeholder="Format: 'Quantity,Unit,Description'"
            />
            <label>Ingredient 4</label>
            <input
              type="text"
              name="ingredient-4"
              placeholder="Format: 'Quantity,Unit,Description'"
            />
            <label>Ingredient 5</label>
            <input
              type="text"
              name="ingredient-5"
              placeholder="Format: 'Quantity,Unit,Description'"
            />
            <label>Ingredient 6</label>
            <input
              type="text"
              name="ingredient-6"
              placeholder="Format: 'Quantity,Unit,Description'"
            />
          </div>
          <button class="btn upload__btn">
            <svg>
              <use href="${icons}#icon-upload-cloud"></use>
            </svg>
            <span>Upload</span>
          </button>
        </form>
        `;
    }




}

export default new UploadRecipeView();